/* Components */
#bg {
  display: block;
  position: fixed;
  top: 0rem;
  left: 0rem;
  width: 100vw;
  height: 100vh;
  z-index: -999; }

.dot {
  display: block;
  width: 3px;
  height: 3px;
  background: -webkit-radial-gradient(#cc97fc, #250245);
  background: radial-gradient(#cc97fc, #250245);
  border-radius: 50%;
  position: absolute; }

.blog {
